.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal-body {
  padding: 25px;
  background: #ccc;
  border-radius: 16px;
  min-width: 250px;
}

.modal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
